// redux-store.ts
import { applyMiddleware, combineReducers, createStore } from "redux";
import { reduserPost } from "./reduer_post";
import { reduserMessages } from "./reduer.messages";
import { reduserName } from "./reduer_name";
import { reduserSudabar } from "./reduer_sibebar";
import { reducerUsers } from "./reduer_users";
import { reduserLogin } from "./reduer_login";
import thunkMiddleware from "redux-thunk";
import { reducer as formReducer } from "redux-form";
import { reducerInitLog } from "./reducer_init";

// add reducer combine
const reduserBox = combineReducers({
  dataPost: reduserPost,
  mesOBJ: reduserMessages,
  names: reduserName,
  sidebar: reduserSudabar,
  usersPage: reducerUsers,
  login:reduserLogin,
  initLogin:reducerInitLog,
  form: formReducer,
});

export type StateType = ReturnType<typeof reduserBox>;

export const store = createStore(reduserBox, applyMiddleware(thunkMiddleware));
